import React from "react"
import Layout from "../componets/layout"
import iconview from "../data/image/product.png"
import Pagination from "../componets/pagination"
import { Link } from "gatsby"
import { ItemListView } from "../componets/item"




const Shop_View_List = ({children} )=>
{
    //const count = 3;
    const data = [0,0,0]
    const datalist = data.map((data, index)=>{

        return(
            <div name="item" key={index} style={{borderBottom: "1px solid #dddddd", marginTop:20, margin:"auto", width:1280, height:181}}>
                <Link to="/product">
                <div style={{marginTop:20, width:140, height:140, float:"left" }}>
                    <img src={iconview} style={{width:140 ,height:140} } alt ="liconview"/>
                </div>

                <div style={{ width:870,marginLeft:20,  marginTop:20, float:"left", display:"inline-block" }}>
                    <strong> [엘제이 캐시미어] </strong>
                    <p> 런칭되자마자 인기리 판매중인 </p>
                    <strong> 9000 원 </strong>
                </div>

                <div style={{borderLeft: "1px solid #dddddd", width:240, height:140, marginTop:20, float:"left", display:"inline-block", textAlign:"center" }}>
                    <br/><br/><br/><br/><br/>
                    <button style={{fontSize:12 ,backgroundColor:"white"}}> 조건부무료 </button>
                    <button style={{marginLeft:10,backgroundColor:"white", fontSize:12}}>   포인트 </button>
                </div>
                </Link>
            </div>

        )
    });

    return(<Layout>



        <div style={{ position:"relative",top :30 ,margin:"auto", width:1280,  textAlign:"left"}}>

            <ItemListView/>

            <div style={{marginTop:40}}/>

                {datalist}

        </div>

        <div style={{}}>

            <Pagination count = {3} link=""/>
        </div>


    </Layout>)
}



export default Shop_View_List